.feedback {
  min-height: 837px;
  background: $color-deep-sky-blue-bg;
  position: relative;
  z-index: 0;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-color: rgba($color-black-perl, 0.5);
    clip-path: polygon(50.5% 0, 0 100%, 0 0);
  }

  @include vp-1023 {
    min-height: 579px;

    &::before {
      display: none;
      min-height: 580px;
    }
  }
}

.feedback__container {
  max-width: 1220px;
  width: 90vw;
  margin: 0 auto;
  padding-top: 163px;
  padding-bottom: 248px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 35px;

  @include vp-1023 {
    display: block;
    width: 87vw;
    max-width: 670px;
    padding-bottom: 0;
    margin-bottom: 0;
    padding-top: 45px;
  }

  @include vp-767 {
    width: 84%;
    min-height: 750px;
    min-width: 270px;
    padding-top: 47px;
    padding-bottom: 85px;
  }
}

.feedback picture {
  display: block;
  padding-top: 80px;
  margin: 0;
  max-width: 510px;

  @include vp-1023 {
    display: none;
  }
}

.feedback__form {
  margin: 0;
  max-width: 100%;

  @include vp-1023 {
    width: 100%;
    min-width: 100%;
    padding: 0;
    margin-left: 0;
  }
}

.feedback h2 {
  margin: 0 0 10px;
  padding: 0;
  font-family: $ff-roboto;
  font-size: 36px;
  font-weight: 400;
  line-height: 51px;
  color: $color-default-white;

  @include vp-1023 {
    margin-bottom: 6px;
  }

  @include vp-767 {
    max-width: 270px;
    margin-bottom: 7px;
    font-size: 30px;
    line-height: 42px;
  }
}

.feedback p {
  margin: 0 0 35px;
  padding: 0;
  font-family: $ff-ptmono;
  font-size: 16px;
  font-style: normal;
  line-height: 22px;
  color: $color-default-white;

  @include vp-1023 {
    margin-bottom: 25px;
  }

  @include vp-767 {
    margin-bottom: 34px;
    font-size: 14px;
    line-height: 19px;
  }
}
